import { Injectable } from '@angular/core';
import { Toast } from '@ionic-native/toast/ngx'; // toast
import { LoadingController, Platform, AlertController, ToastController } from '@ionic/angular'; // loading
import { HttpClient, HttpParams, HttpHeaders } from '@angular/common/http'; //  

@Injectable({
  providedIn: 'root'
})
export class HttpUtilsService {

  public toast: any = null;
  constructor(
    private cordovaToast: Toast,
    public toastCtrl: ToastController,
    public loadingController: LoadingController,
    private httpClient: HttpClient) { }

  /**
    * 
    */
  Toast(txt: string, position?: string) {
    try {
      this.cordovaToast.show(txt, '2000', position ? position : 'center').subscribe();
    } catch (error) {
      // console.log(error);
    }
  }

  /**
   * toast
   **/
  async thsToast(txt: string, position?) {
    if (this.toast) {
      this.toast.dismiss();
      this.toast = null;
    }
    this.toast = await this.toastCtrl.create({
      message: txt,
      duration: 2000,
      position: position ? position : 'bottom',
      cssClass: 'toast-message',
    });
    this.toast.present();
  }

  /**
   * showloading
   * @param template ()
   */
  async presentLoading(template?) {
    const loader = await this.loadingController.create({
      spinner: 'circles',
    });
    await loader.present();
    return loader;
  }

  /**
   * loading
   * @param  loader loading
   */
  hideLoading(loader) {
    loader.dismiss();
  }

  /**
   * GET
   * @param  url ()
   * @param  params ()
   * @param  showloading Loading()boolean
   * @param  callback ()
   */
  async get(url, params, showloading, callback) {
    let loader = null;
    if (showloading) {
      loader = await this.presentLoading('');
    }

    this.httpClient.get(url, { responseType: 'json', params: this.httpParams(params) })
      .subscribe(
        res => {
          if (showloading) {
            this.hideLoading(loader);
          }
          if (res) {
            callback(res);
          } else {
            this.thsToast('');
            callback('error');
          }
        },
        err => {
          if (showloading) {
            this.hideLoading(loader);
          }
          this.thsToast('');
          callback('error');
        }
      );
  }

  /**
   * HTTPpost
   * @param  url 
   * @param  params 
   * @param  success 
   * @param  error 
   * @param  flag ,falsetrue
 */
  async post(url, params, showloading, callback) {
    let loader = null;
    if (showloading) {
      loader = await this.presentLoading('');
    }
    this.httpClient.post(
      url,
      params
      // this.httpParams(params),
      // this.httpOptions() // header
    ).subscribe(
      res => {
        if (showloading) {
          this.hideLoading(loader);
        }
        if (res) {
          callback(res);
        } else {
          this.thsToast('');
          callback('error');
        }
      },
      err => {
        if (showloading) {
          this.hideLoading(loader);
        }
        this.thsToast('');
        callback('error');
      }
    );
  }

  /**
   * 
   */
  httpOptions() {
    return {
      headers: new HttpHeaders({
        'Content-Type': 'application/x-www-form-urlencoded'
      })
    };
  }

  /**
  * params
  * @param  param 
  */
  httpParams(param: Map<any, any>) {
    let ret = new HttpParams();
    if (param) {
      for (const key in param) {
        if (param[key]) {
          ret = ret.set(key, param[key]);
        } else {
          ret = ret.set(key, param[key]);
        }
      }
    }
    return ret;
  }

}

