import { Component } from '@angular/core';
import { AppMinimize } from '@ionic-native/app-minimize/ngx';
import { Platform } from '@ionic/angular';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { StatusBar } from '@ionic-native/status-bar/ngx';
import { AppUpdateService } from './services/public/app-update.service';
import { HttpUtilsService } from './services/public/http-utils.service';

@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html'
})
export class AppComponent {
  public backButtonPressed = false; // 

  constructor(
    private platform: Platform,
    private splashScreen: SplashScreen,
    private appMinimize: AppMinimize,
    private statusBar: StatusBar,
    private httpUtilsService: HttpUtilsService,
  ) {
    this.initializeApp();
  }

  initializeApp() {
    this.platform.ready().then(() => {
      // this.statusBar.styleDefault();
      setTimeout(() => {
        this.splashScreen.hide();
        this.registerBackButtonAction();
      }, 100);
    });
  }
  registerBackButtonAction() {
    // tabs
    const tab = '/app/tabs';
    const login = '/login';
    this.platform.backButton.subscribe(() => {
      const url = this.handleUrl(this.platform.url());
      if (url.indexOf(tab) !== -1 || url.indexOf(login) !== -1) {
        this.showExit();
      }
    });
  }
  /**
     * url
     * @param url url
     */
  handleUrl(url) {
    if (!url) {
      return url;
    }
    const rel = url.split('//')[1].split('?')[0];  // http://
    let newUrl = '';
    for (let i = 0, len = rel.length; i < len; i++) {
      if (rel[i] === '/') { // 
        newUrl = rel.substring(i);
        break;
      }
    }
    return newUrl;
  }

  // 
  showExit() {
    // true
    if (this.backButtonPressed) {
      this.backButtonPressed = false;
      this.appMinimize.minimize();
    } else {
      // Toast
      this.httpUtilsService.thsToast('');
      // true
      this.backButtonPressed = true;
      // false
      setTimeout(() => this.backButtonPressed = false, 2000);
    }
  }
}
