import { HttpEvent, HttpInterceptor, HttpRequest, HttpHandler, HTTP_INTERCEPTORS } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, throwError, of } from 'rxjs';
import { catchError, timeout } from 'rxjs/operators';
import { ToastController } from '@ionic/angular';
import { HttpUtilsService } from '../public/http-utils.service';

/*
  .
*/
@Injectable()
export class HttpReqInterceptorService implements HttpInterceptor {
  public constructor(
    public toastController: ToastController,
    public httpUtils: HttpUtilsService
  ) { }

  defaultTimeout = 8000; // 
  intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    // 
    const timevalue = Number(req.headers.get('timeout')) || this.defaultTimeout;
    const authReq = req.clone({
      headers: req.headers.set('Content-type', 'application/x-www-form-urlencoded;charset=utf-8').delete('timeout'),
      // params: req.params.set('token', this.configProvider.token) // token 
    }); // 
    // throw new Error('Method not implemented.');
    return next.handle(authReq).pipe(
      timeout(timevalue),
      catchError(e => {
        if (e.name === 'TimeoutError') {// 
          this.httpUtils.thsToast('');
        }
        return throwError(e);
      }));
  }
  /**
   * 
   * @param text  
   */
  // async presentToast(text) {
  //   const toast = await this.toastController.create({
  //     message: text,
  //     duration: 2000
  //   });
  //   toast.present();
  // }
}

export const HttpReqInterceptorProvider = {
  provide: HTTP_INTERCEPTORS,
  useClass: HttpReqInterceptorService,
  multi: true
};
