import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'genPositionStr'
})
export class GenPositionStrPipe implements PipeTransform {
  getS;//
  transform(value: any, args?: any): string {
    const getNum=Math.floor(value) ;//
    this.getS=( value - getNum ) * 3600 ;
    this.getS=parseInt( this.getS );//
    const getM=(this.getS)/60;//
    this.getS=(this.getS)%60;//
    return getNum+','+getM+','+this.getS+',';
  }

}
